import {
	mapGetters
} from 'vuex';
export default {
	props: {
		showSpace: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			height: 0
		}
	},
	created() {
		var sysinfo = uni.getSystemInfoSync(),
			windowHeight = sysinfo.windowHeight;
		//减去底部导航高度
		this.height = windowHeight - 50;
	},
	computed: {
		...mapGetters('utp', ['navHeight'])
	}
}
